:root {
  --accent: #0C8543;
  --accent-bg: #F3F9F6;
  --light: #ffffff;
  --text-color: #000000;
  --radius: 8px;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a:focus-visible,
button:focus-visible {
  color: #1b1b1b;
  outline: 3px solid #0C8543;
  outline: 3px solid var(--accent);
  outline-offset: 3px;
}

body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  margin: 0;
  letter-spacing: 0.02em;
  color: #000000;
  color: var(--text-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

._ibg {
  position: relative;
}

._ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo img {
  display: inline-block;
}

.accent {
  color: var(--accent);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
}

.pageTitle {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 10px;
}

.pageSubtitle {
  margin-bottom: 10px;
}

