.header {
  padding: 10px 0;
  border-bottom: 1px solid #E0E0E0;
}

.offer {
  padding: 25px 0 35px;
}

.offer__title {
  min-height: 165px;
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 25px;
}

.offer__inner {
  flex-wrap: wrap;
  margin: -4px;
}

.offer__card {
  margin: 4px;
  flex-basis: calc(50% - 8px);
}

.offerCard {
  padding: 20px;
  font-size: 16px;
  line-height: 1.3;
  border-radius: 8px;
  border-radius: var(--radius);
  background-color: #F3F9F6;
  background-color: var(--accent-bg);
  text-align: center;
}

.offerCard__img {
  width: 60px;
  height: 60px;
  margin: 0 auto 15px;
}

.offerCard__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.benefits {
  padding: 35px 0;
  background: #F3F9F6;
  background: var(--accent-bg);
}

.benefits__item {
  margin: 0 0 30px 0;
}

.benefits__item:last-child {
  margin: 0;
}

.benefitsItem__text {
  margin: 0 0 20px 0;
}

.benefitsItem__image {
  overflow: hidden;
  border-radius: 8px;
  border-radius: var(--radius);
  padding-bottom: 51%;
}

.benefitImage {
  position: relative;
}

.benefitImage__badge {
  position: absolute;
  top: 10px;
  right: 10px;
}

.benefitImage__preview {
  position: relative;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  overflow: hidden;
}

.benefitImage__description {
  max-width: 200px;
  background: #777;
  color: #ffffff;
  color: var(--light);
  font-size: 10px;
  padding: 5px 15px 5px 5px;
  text-transform: uppercase;
  transform: translateX(10px);
}

.prices {
  padding: 60px 0;
  text-align: center;
}

.pageCard {
  height: 100%;
  border-radius: 8px;
  border-radius: var(--radius);
  overflow: hidden;
  font-size: 21px;
  line-height: 1.3;
  text-align: center;
  color: #ffffff;
  color: var(--light);
  background: #0C8543;
  background: var(--accent);
}

.pageCard__img {
  padding-bottom: 50%;
  border-radius: 8px;
  border-radius: var(--radius);
  overflow: hidden;
}

.pageCard__text {
  padding: 15px;
}

.warranty {
  text-align: center;
}

.warranty__body {
  background-color: #F6F6F6;
  padding: 35px 0;
}

.warranty__image {
  width: 100%;
  padding-bottom: 100%;
  margin: 0 auto 20px;
}

.warranty__image img {
  object-fit: contain;
}

.warranty__note {
  padding: 35px 0;
  text-align: left;
}

.footer {
  padding: 20px 0;
  font-size: 14px;
  color: #ffffff;
  color: var(--light);
  text-align: center;
  background-color: #0C8543;
  background-color: var(--accent);
}

.footer .logo {
  margin: 0 0 25px 0;
}

.pageNav li {
  margin: 0 0 15px 0;
  text-decoration: underline;
}

.pageNav li:hover {
  text-decoration: none;
}

.copyright {
  padding-top: 10px;
}

@media screen and (min-width: 540px) {
  .offer__card {
    font-size: 18px;
    padding: 25px;
  }
}

